@import "var";
@import "mixins";

.site-list__toggle {
  position: absolute;
  display: block;
  top: -10px;
  left: 0;
  width: 50px;
  height: 50px;
  border: none;
  background: transparent;
  cursor: pointer;

  &:after {
    content: "";
    position: absolute;
    top: 16px;
    left: 50%;
    width: 30px;
    height: 2px;
    transform: translateX(-50%);
    background-color: $dark;
    box-shadow: 0 8px 0 0 $dark, 0 16px 0 0 $dark;

    transition: all 0.3s linear;
  }
}

.site-list__toggle--active {
  top: -10px;
  left: 0;

  &::before,
  &::after {
    content: "";
    position: absolute;
    top: 26px;
    left: 10px;
    width: 30px;
    height: 2px;
    background-color: $dark;
  }
  &::before {
    transform: rotate(45deg);
  }
  &::after {
    transform: rotate(-45deg);
    box-shadow: none;
  }
}

.site-list--closed {
  display: none;
}

.site-list--nojs {
  display: block;
}

.site-list--open {
  display: block;

  position: absolute;
  top: 50px;
  left: 0px;
  z-index: 100;

  flex-direction: column;
  background: $light;
  box-shadow: 0 6px 0 0 $dark, 0 12px 0 0 $dark;

  animation: show 0.5s linear;
}

@keyframes show {
  0% {
    height: 0;
    opacity: 0.5;
    overflow: hidden;
  }
  100% {
    height: 200px;
    opacity: 1;
  }
}
.site-list__item {
  margin-bottom: 20px;
  padding: 6px;
}

@media (min-width: $tablet) {
  .site-list__toggle {
    display: none;
  }
  .page-header .site-list {
    display: flex;
    flex-direction: row;
    position: absolute;
    top: 0;
    left: 45px;
  }
}

