@import "var";
@import "mixins";

.dropdown {
  position: relative;
  text-align: right;
}

.dropdown__btn {
  position: relative;
  margin: 30px 0 40px;
  padding-left: 18px;
  width: 290px;
  height: 50px;
  background: transparent;
  border: solid 2px $dark;
  box-sizing: border-box;

  font-size: 18px;
  line-height: 21px;
  text-align: left;

  cursor: pointer;
  &:hover,
  &:focus {
    background: $light-grey;
  }

  &::after,
  &::before {
    content: "";
    position: absolute;
    top: 20px;
    width: 2px;
    height: 10px;

    background: $dark;
  }
  &::after {
    right: 21px;
    transform: rotate(140deg);
  }

  &::before {
    right: 15px;
    transform: rotate(-140deg);
  }
}
.dropdown-inner {
  width: 100%;
  border-bottom: solid 1px $light-grey;
  &::after {
    position: absolute;
    bottom: 0;
    left: 0;
    width: 100%;
    height: 1px;
    background: red;
  }
}
.dropdown-inner .dropdown__btn {
  border: none;
  width: 100%;
  margin: 0;

  font-size: 16px;
}
.dropdown-inner .dropdown__content {
  position: static;
  border: none;
  box-shadow: none;
  width: 100%;

  text-align: left;
  padding: 18px;
  box-sizing: border-box;
}
.dropdown__btn--active {
  &::after {
    transform: rotate(45deg);
  }

  &::before {
    transform: rotate(-45deg);
  }
}

.dropdown__content {
  display: none;
  position: absolute;
  top: 80px;
  right: 15px;
  background-color: $light;
  width: 290px;
  box-shadow: $card-shadow;
  box-sizing: border-box;
  border: solid 2px $dark;
  border-top: none;
  z-index: 1;
}

.dropdown__content--active {
  display: block;
}

.dropdown .button {
  width: 100%;
  &:hover,
  &:focus {
    box-shadow: none;
    transform: none;
    background-color: rgba($color: $dark, $alpha: 0.8);
  }
}
.dropdown .clear {
  background: $light;
  color: $dark;
  border-right: solid 2px;
  &:hover,
  &:focus {
    background-color: rgba($color: $grey, $alpha: 0.3);
  }
}
.dropdown__content--active.dropdown__collection {
  display: flex;
  flex-wrap: wrap;
}
.dropdown__link {
  display: block;
  width: 35%;
  margin-bottom: 20px;
  font-size: 14px;
  line-height: 16px;

  color: $grey;
  &:hover,
  &:focus {
    text-decoration: none;
    color: $dark;
  }
}

.dropdown__link--active {
  color: $dark;
}

@media (min-width: $tablet) {
  .dropdown__btn {
    width: 206px;
  }
  .dropdown__content {
    width: 206px;
    right: 45px;
  }
}
@media (min-width: $desktop) {
  .dropdown__btn {
    width: 243px;
  }

  .dropdown__content {
    width: 243px;
  }
}
