body {
  margin: 0 auto;
  min-width: 320px;
  background: $light;
  font-family: "Work Sans", sans-serif;
  font-style: normal;
  font-weight: normal;
  font-size: 24px;
  line-height: 16px;
  color: $dark;
}
.main-page {
  margin: 0 auto;
}
a {
  font-family: Work Sans;
  font-style: normal;
  font-weight: 500;
  font-size: 16px;
  line-height: 19px;
  color: $dark;
  text-decoration: none;
  &:hover {
    text-decoration: underline;
  }
}

.logo {
  display: block;
  width: 100%;
  box-sizing: border-box;

  font-weight: 800;
  font-size: 24px;
  line-height: 28px;
}

.button {
  display: block;
  min-width: 204px;
  min-height: 64px;
  padding: 27px 17px;
  box-sizing: border-box;

  border: none;
  background: $dark;
  color: $light;

  font-weight: bold;
  font-size: 16px;
  line-height: 19px;

  cursor: pointer;
  transition: all 0.3s linear;
  &:hover {
    @include shadow-transform;
  }
  &:active {
    transform: scale(1.1);
  }
}

.visually-hidden:not(:focus):not(:active) {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}

@media (min-width: $mobile) {
  .main-page {
    width: $mobile;
  }
}

@media (min-width: $tablet) {
  .main-page {
    width: $tablet;
  }
}

@media (min-width: $desktop) {
  .main-page {
    width: $desktop;
  }
}
