@import "var";
@import "mixins";

.page-header {
  position: relative;
  height: 47px;
}
.page-header__logo {
  text-align: center;
  width: 200px;
  margin: 0 auto;
}

@media (min-width: $mobile) {
  .page-header {
    margin: 20px auto 0;
    width: $mobile;
  }
}

@media (min-width: $tablet) {
  .page-header {
    width: $tablet;
  }
}

@media (min-width: $desktop) {
  .page-header {
    width: $desktop;
  }
}
