@import "var";
@import "mixins";

.catalog-nav {
  @include reset-list;
  display: flex;
  max-width: 500px;
}

.catalog-nav__item:first-child {
  display: none;
}
.catalog-nav__item a {
  position: relative;
  margin-right: 30px;

  font-size: 18px;
  line-height: 21px;
  color: $grey;

  &::after {
    content: "/";
    position: absolute;
    right: -19px;
  }
}

.catalog-nav__item--current a {
  color: $dark;
  &:after {
    content: "";
  }
}

@media (min-width: $mobile) {
}

@media (min-width: $tablet) {
  .catalog-nav__wrapper {
    background: $light-grey;
  }
  .catalog-nav__section {
    position: relative;
  }
  .catalog-nav {
    position: absolute;
    top: 60px;
  }
  .catalog-nav__item:first-child {
    display: block;
  }
}

@media (min-width: $desktop) {
  .catalog-nav {
    left: 237px;
  }
}
