@import "var";
@import "mixins";

.page-about__title {
  width: 300px;
  font-size: 28px;
  line-height: 125%;

  margin-right: 100px;
  margin-left: auto;
}

.page-about__img {
  position: relative;
  margin-left: auto;
  margin-right: 30px;
  width: 294px;
  height: 260px;

  &::before,
  &::after {
    position: absolute;
    content: "";

    width: 178px;
    height: 135px;
    background: $striped-gradient;
  }
  &::before {
    top: 0px;
    left: -30px;
  }
  &::after {
    bottom: 0px;
    right: -30px;
  }
  img {
    display: block;
    width: 100%;
    position: relative;
    z-index: 1;
  }
}

@media (min-width: $mobile) {
  .page-about {
    display: none;
  }
}
@media (min-width: $tablet) {
  .page-about {
    display: flex;
    align-items: center;
  }
}
@media (min-width: $desktop) {
  .page-about__img {
    width: 299px;
    height: 260px;
  }
  .page-about__title {
    font-size: 32px;
  }
}
