@import "var";
@import "mixins";

@mixin shadow-color($color) {
  background: $color;
  box-shadow: 0 0 0 2px $light, 0 0 0 3px $color;
}

$light-grey: #b0c3c7;
$grey: #bfc3c8;
$light-blue: #4193da;
$deep-blue: #3b6392;
$white: #ececec;
$black: #2d2e2e;

fieldset {
  margin: 0;
  padding: 0 !important;
  border: none;
}

.filter-color {
  display: flex;
}
.filter-color__field {
  width: 30px;
  margin: 0;
  padding: 0;
  margin-bottom: 20px;
}
.filter-color__field label {
  display: inline-block;
  width: 16px;
  height: 16px;

  border-radius: 50%;
  cursor: pointer;
}

.filter-color__field input[type="checkbox"] {
  display: none;
}

.filter-color__field label[for="light-grey"] {
  background: $light-grey;
}
.filter-color__field label[for="grey"] {
  background: $grey;
}

.filter-color__field label[for="deep-blue"] {
  background: $deep-blue;
}

.filter-color__field label[for="light-blue"] {
  background: $light-blue;
}

.filter-color__field label[for="white"] {
  background: $white;
}

.filter-color__field label[for="black"] {
  background: $black;
}

.filter-color__field input#light-grey:checked + label {
  @include shadow-color($light-grey);
}
.filter-color__field input#grey:checked + label {
  @include shadow-color($grey);
}

.filter-color__field input#black:checked + label {
  @include shadow-color($black);
}
.filter-color__field input#white:checked + label {
  @include shadow-color($white);
}

.filter-color__field input#deep-blue:checked + label {
  @include shadow-color($deep-blue);
}
.filter-color__field input#light-blue:checked + label {
  @include shadow-color($light-blue);
}

@media (min-width: $tablet) {
  .filter-color {
    flex-wrap: wrap;
  }

  .filter-color__field {
    width: 26%;
    margin-bottom: 20px;
  }
}

@media (min-width: $desktop) {
  .filter-color {
    flex-wrap: nowrap;
  }

  .filter-color__field {
    width: 30px;
  }
}
