@import "var";

.user-list {
  display: flex;
}

.user-list__link {
  margin-left: 25px;
  svg {
    transition: all 0.3s linear;
    &:hover {
      transform: translate(2px, 2px);
      filter: drop-shadow(-2px -2px 2px rgba(0, 0, 0, 0.2));
    }
    &:active {
      transform: scale(1.1);
    }
  }
}

.user-list__link--search {
  svg {
    width: 16px;
    height: 16px;
  }
}

.user-list__link--basket {
  svg {
    width: 18px;
    height: 16px;
  }
}

.page-header .user-list {
  position: absolute;
  top: 5px;
  right: 0;
}
@media (min-width: $tablet) {
  .page-header .user-list {
    right: 45px;
  }
}
@media (min-width: $desktop) {
  .user__list {
    flex-direction: column;
  }
  .user-list__item {
    margin-bottom: 35px;
  }
  .page-header .user-list {
    flex-direction: row;
  }
}
