@import "var";
@import "mixins";

.catalog-grid__list {
  @include reset-list;
  display: grid;
  grid-template-columns: 130px 130px;
  grid-template-rows: auto auto;
  grid-column-gap: 30px;
  grid-row-gap: 30px;
}

.catalog-grid__item {
  img {
    display: block;
    width: 100%;
    height: 160px;
    object-fit: cover;
  }
}

.catalog-grid__big-item {
  grid-column-start: 1;
  grid-column-end: 3;
  grid-row-start: 3;
  grid-row-end: 5;
  img {
    height: 442px;
  }
}

@media (min-width: $tablet) {
  .catalog-grid__list {
    grid-template-columns: 206px 206px 206px;
    grid-row-gap: 50px;
  }
  .catalog-grid__item img {
    height: 279px;
  }
  .catalog-grid__big-item {
    grid-column-start: 2;
    grid-column-end: 4;
    grid-row-start: 2;
    grid-row-end: 4;
    img {
      height: 672px;
    }
  }
}
@media (min-width: $desktop) {
  .catalog-grid__list {
    grid-template-columns: 243px 243px 243px 243px;
    grid-column-gap: 45px;
    grid-row-gap: 66px;
  }
  .catalog-grid__item img {
    height: 270px;
  }
  .catalog-grid__big-item {
    grid-column-start: 3;
    grid-column-end: 5;

    img {
      height: 670px;
    }
  }
}
